import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatDividerModule } from "@angular/material/divider";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from "@angular/material/legacy-checkbox";
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from "@angular/router";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { BreadCrumbModule, LoadingModule, SettingsListModule } from "@tei/material";
import { RangeServantService } from "../../../@services/range-servant.service";
import { CreateRangeClassIDDialogComponent } from "./create-range-class-id-dialog/create-range-class-id-dialog.component";
import { RangeMachineClassIDSComponent } from "./range-machine-class-ids.component";

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatCheckboxModule, MatButtonModule, MatDividerModule, MatInputModule, MatButtonModule, MatAutocompleteModule,
  MatCardModule, MatTableModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, SettingsListModule, LoadingModule];
const COMPONENTS = [RangeMachineClassIDSComponent, CreateRangeClassIDDialogComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [COMPONENTS],
  imports: [
    RouterModule.forChild([{ path: '', component: RangeMachineClassIDSComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [{ provide: 'IRangeServantService', useClass: RangeServantService }]
})

export class RangeMachineClassIDSModule { }
