import { Component, Inject } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { RangeItemClass } from '../../../@intermediate/models/get-range-machine-class-ids.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { IRangeServantService } from '../../../@intermediate/range-servant.interface';
import { CreateRangeClassIDDialogComponent } from './create-range-class-id-dialog/create-range-class-id-dialog.component';

@Component({
  selector: 'tei-admin-center-range-machine-class-ids',
  templateUrl: './range-machine-class-ids.component.html',
  styleUrls: ['./range-machine-class-ids.component.scss']
})
export class RangeMachineClassIDSComponent
{
  breadCrumbData: BreadCrumb[] = [];
  rangeItemClasses: RangeItemClass[] = [];
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  tableColumns: string[] = ["ItemClassID", "Actions", "MobileMode"];
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject("IRangeServantService") private rangeServantService: IRangeServantService,
    private translate: TranslateService,
    private dialog: MatDialog,
    private snackBar: SnackBarService,
    private confirmDialogService: ConfirmDialogService,
    public manageLoadingService: ManageLoadingService)
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.loadData();
      }
    }));

  }

  async loadData(): Promise<void>
  {
    try
    {
      const response = await firstValueFrom(this.rangeServantService.GetRangeItemClasses({ PortalID: this.portalDetail.portal.PortalID }));
      this.rangeItemClasses = response.RangeItemClasses;
    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  onDeleteItem(id: number): void
  {
    const dialogData: DialogData = {
      title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.DELETE'),
      message: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.DELETE_CONFIRM_DESCRIPTION'),
      textOkayButton: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.DELETE'),
      textCancelButton: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.CANCEL'),
      buttonColor: 'warn'
    };
    const dialogRef = this.confirmDialogService.openDialog(dialogData, { maxWidth: '350px' });

    dialogRef.afterClosed().subscribe(async result =>
    {
      if (result)
      {
        try
        {
          await firstValueFrom(this.rangeServantService.DeleteRangeItemClasses({
            PortalID: this.portalDetail.portal.PortalID,
            ID: id
          }));
          this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.DElETED_SUCCESSFULLY'), '', null, 'left');
          this.loadData();
        }
        catch (error)
        {
          console.error(error);
        }
        finally
        {
          this.manageLoadingService.finishLoading();
        }
      }
    });
  }

  onCreateItem(): void
  {
    const data = {
      PortalID: this.portalDetail.portal.PortalID
    };
    const dialog = this.dialog.open(CreateRangeClassIDDialogComponent, {
      width: '400px',
      panelClass: ['o-dialog-sm-fullscreen'],
      data
    });
    this.subscription.add(dialog.afterClosed().subscribe((isSuccess: boolean) =>
    {
      if (isSuccess)
      {
        this.loadData();
      }
    }));
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGEMACHINEINTEGRATION'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.RANGE_MACHINE_CLASS_IDS'),
        relativeUrl: '',
      }
    ];
  }
}
