import { ChangeDetectionStrategy, Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService, ResponsiveService } from "@tei/core";
import { SnackBarService } from "@tei/material";
import { debounceTime, distinctUntilChanged, filter, firstValueFrom, Subscription, switchMap } from "rxjs";
import { ClassItemDialogData, ClassItemForm, GetItemClassesRequest, RangeItemClass } from "../../../../@intermediate/models/get-range-machine-class-ids.model";
import { IRangeServantService } from "../../../../@intermediate/range-servant.interface";
@Component({
  selector: 'tei-admin-center-create-class-id-dialog',
  templateUrl: './create-range-class-id-dialog.component.html',
  styleUrls: ['./create-range-class-id-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CreateRangeClassIDDialogComponent implements OnInit, OnDestroy
{
  classItemForm = new FormGroup<ClassItemForm>({
    ItemClassID: new FormControl<RangeItemClass>(null, Validators.required),
  });
  filteredOptions: RangeItemClass[];
  isMobile = false;
  subscriptions = new Subscription();
  dialogData: ClassItemDialogData;
  constructor(
    public responsiveService: ResponsiveService,
    public dialogRef: MatDialogRef<CreateRangeClassIDDialogComponent>,
    public manageLoadingService: ManageLoadingService,
    private snackBar: SnackBarService,
    @Inject(MAT_DIALOG_DATA) public data: ClassItemDialogData,
    @Inject("IRangeServantService") private rangeServantService: IRangeServantService,
    private translate: TranslateService
  )
  {
  }

  async ngOnInit(): Promise<void>
  {
    this.loadItems();
    this.loadRangeItemClass();
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'sm' || screenSize === 'xs';
  };

  async loadRangeItemClass(): Promise<void>
  {
    const control = this.classItemForm.get('ItemClassID');
    if (!control) return;
    const salespersonValueChanges$ = control.valueChanges.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(value => typeof value === 'string'),
      switchMap(value => this.loadItems(value))
    );
    this.subscriptions.add(salespersonValueChanges$.subscribe());
  }

  displayFn(value: RangeItemClass): string
  {
    return value?.ClassID;
  }

  async loadItems(filter?: any): Promise<void>
  {
    try
    {
      const request: GetItemClassesRequest = { PortalID: this.data.PortalID };
      if (filter)
      {
        request.ClassIDFilter = filter;
      }
      this.manageLoadingService.startLoading();
      const response = await firstValueFrom(this.rangeServantService.GetItemClasses(request));
      this.filteredOptions = response.ItemClasses;
    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  async save(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      await firstValueFrom(this.rangeServantService.PostRangeItemClasses({
        PortalID: this.data.PortalID,
        ItemClassID: this.classItemForm.value.ItemClassID.ClassID,
      }));
      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_CLASS_IDS.SAVED_SUCCESSFULLY'), '', null, 'left');

      this.dialogRef.close(true);
    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}
