import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteRangeServantRequest, DeleteRangeServantResponse } from '../@intermediate/models/delete-range-machine-class-ids..model ';
import { GetItemClassesRequest, GetItemClassesResponse, GetRangeItemClassesRequest, GetRangeItemClassesResponse } from '../@intermediate/models/get-range-machine-class-ids.model';
import { GetRangeServantRequest, GetRangeServantResponse } from '../@intermediate/models/get-range-servant.model';
import { PostRangeItemClassesRequest, PostRangeItemClassesResponse } from '../@intermediate/models/post-range-machine-class-ids.model';
import { PostRangeServantRequest, PostRangeServantResponse } from '../@intermediate/models/post-range-servant.model';
import { IRangeServantService } from '../@intermediate/range-servant.interface';

@Injectable()
export class RangeServantService implements IRangeServantService
{

  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/RangeServantIntegration';
  }
  GetRangeItemClasses(request: GetRangeItemClassesRequest): Observable<GetRangeItemClassesResponse>
  {
    const apiUrl = this.serviceUrl + '/GetRangeItemClasses';
    return this.http.get<GetRangeItemClassesResponse>(apiUrl, { params: request as any }
    );
  }
  GetItemClasses(request: GetItemClassesRequest): Observable<GetItemClassesResponse>
  {
    const apiUrl = this.serviceUrl + '/GetItemClasses';
    return this.http.get<GetItemClassesResponse>(apiUrl, { params: request as any }
    );
  }
  PostRangeItemClasses(request: PostRangeItemClassesRequest): Observable<PostRangeItemClassesResponse>
  {
    const apiUrl = this.serviceUrl + '/PostRangeItemClasses';
    return this.http.post<PostRangeItemClassesResponse>(apiUrl, request);
  }
  DeleteRangeItemClasses(request: DeleteRangeServantRequest): Observable<DeleteRangeServantResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + '/DeleteRangeItemClasses';
    return this.http.delete<DeleteRangeServantResponse>(apiUrl, options);
  }
  Get(request: GetRangeServantRequest): Observable<GetRangeServantResponse>
  {
    const apiUrl = this.serviceUrl + '/GetRangeServant';
    return this.http.get<GetRangeServantResponse>(apiUrl, { params: request as any }
    );
  }
  Post(request: PostRangeServantRequest): Observable<PostRangeServantResponse>
  {
    const apiUrl = this.serviceUrl + '/PostRangeServant';
    return this.http.post<PostRangeServantResponse>(apiUrl, request);
  }
}
